@use "../../global_variables" as _;

.round_button {
    &.dangerous {
        @media (hover: hover) {
            &:not(.disabled):hover {
                color: _.$white;
                background-color: _.$red;
            }
        }
    }
    &.active {
        &:not(.disabled) {
            color: _.$deep-gray;
        }
        &.primary {
            &:not(.disabled) {
                color: _.$blue;
                background-color: _.$transparent-blue;
            }
        }
    }
    &.p-8 {
        padding: 8px !important;
    }
}
