@use "../../global_variables" as _;

.button {
    $border-radius: 5px;
    $transition-duration: 150ms;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: 5px;
    border: none;
    border-radius: $border-radius;
    padding: 5px 12px;
    width: max-content;
    min-width: max-content;
    height: fit-content;
    transition-duration: $transition-duration;
    letter-spacing: _.$base-letter-spacing;
    user-select: none;
    cursor: pointer;
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: $border-radius;
        background-color: transparent;
        transition-duration: $transition-duration;
    }
    @media (hover: hover) {
        &:hover:not(:disabled) {
            &::before {
                background-color: _.$transparent-black;
            }
        }
    }
    &:disabled {
        cursor: not-allowed !important;
        color: _.$light-gray !important;
        background-color: _.$noisy-white !important;
    }
    &.clean {
        background-color: transparent;
        color: _.$deep-gray;
    }
    &.light {
        background-color: _.$noisy-white;
        color: _.$deep-gray;
    }
    &.black_fill {
        background-color: _.$black;
        color: _.$white;
    }
    &.primary_fill {
        background-color: _.$blue;
        color: _.$white;
    }
    &.primary {
        background-color: _.$white;
        color: _.$blue;
    }
    &.success_fill {
        background-color: _.$green;
        color: _.$white;
    }
    &.success {
        background-color: _.$white;
        color: _.$green;
    }
    &.dangerous_fill {
        background-color: _.$red;
        color: _.$white;
    }
    &.dangerous {
        background-color: _.$white;
        color: _.$red;
    }
    &.p8-15 {
        padding: 8px 15px !important;
    }
    &.xl {
        font-size: 1.1 * _.$base-font-size;
        padding: 12px 25px;
        gap: 12px;
    }
    &.l {
        font-size: 1 * _.$base-font-size;
        padding: 8px 20px;
        gap: 8px;
    }
    &.s {
        font-size: 0.8 * _.$base-font-size;
        padding: 4px 10px;
    }
    &.xs {
        font-size: 0.7 * _.$base-font-size;
        padding: 3px 8px;
        gap: 3px;
    }
    &.border {
        border: 1px solid _.$light-gray;
    }
    &.bold {
        font-weight: bold;
    }
}
